<style>
  .echarts {
    width: 100%;
    height: 100%;
  }
</style>

<template>
  <div>
    <H1>雅居乐花园"四返"人员信息</H1>
  <chart  :options="region"/>
  <chart  :options="isolate"/>
  <chart  :options="line"/>
  </div>
</template>

<script>
  import 'echarts/lib/chart/bar'
  import 'echarts/lib/chart/line'
  import 'echarts/lib/component/tooltip'
  import 'echarts/lib/component/polar'
  import 'echarts/lib/component/legend'
  import 'echarts/lib/component/title.js'
  import 'echarts/lib/chart/pie'


  export default {
    name:"HelloWorld",
    data () {
      return {
        region: {
          title : {
            text: '"四返"人员出发地信息统计',
            subtext: '动态数据',
            x:'left'
          },
          tooltip : {
            trigger: 'item',
            formatter: "{a} <br/>{b} : {c}人 ({d}%)"
          },
          legend: {
            show: true,
            orient: 'horizontal',
            bottom: '20px',
            data: ['湖北武汉','疫情重点区','其他四返人员']
          },
          series : [
            {
              name: '此地区人员数量',
              type: 'pie',
              radius : '50%',
              center: ['50%', '50%'],
              data:[
                {value:95, name:'疫情重点区'},
                {value:20, name:'湖北武汉'},
                {value:135, name:'其他四返人员'}
              ],
              itemStyle: {
                emphasis: {
                  shadowBlur: 10,
                  shadowOffsetX: 0,
                  shadowColor: 'rgba(0, 0, 0, 0.5)'
                }
              }
            }
          ]
        },
        isolate: {
          title : {
            text: '解除隔离人数占比',
            subtext: '动态数据',
            x:'left'
          },
          tooltip : {
            trigger: 'item',
            formatter: "{a} <br/>{b} : {c}人 ({d}%)"
          },
          legend: {
            show: true,
            orient: 'horizontal',
            bottom: '20px',
            data: ['隔离人数','解除隔离人数']
          },
          series : [
            {
              name: '此类人员数量',
              type: 'pie',
              radius : '50%',
              center: ['50%', '50%'],
              label: {
                formatter: '{b}：{c}人 '}
                ,
              data:[
                {value:95, name:'正在隔离人数'},
                {value:20, name:'解除隔离人数'}
              ],
              itemStyle: {
                emphasis: {
                  shadowBlur: 10,
                  shadowOffsetX: 0,
                  shadowColor: 'rgba(0, 0, 0, 0.5)'
                }
              }
            }
          ]
        },
        line:{
          title : {
            text: '隔离人数变化趋势',
            subtext: '动态数据',
            x:'left'
          },
          xAxis: {
            type: 'category',
            data: ['2月1日', '2月2日', '2月3日', '2月4日', '2月5日', '2月6日', '2月7日']
          },
          yAxis: {
            type: 'value'
          },
          series: [{
            data: [200, 180, 174, 153, 133, 121, 110],
            type: 'line'
          }]
        }

      }
    }
  ,
  methods: {
    loadData () {
      if (this.canLoad && !this.loading) {
        this.loading = true
        this.$axios.get('https://geckos.top/api/members' + '?page=' + this.page)
          .then(res => {
            if (res.data.length > 0) {
              this.imgsArr = this.imgsArr.concat(res.data)
              this.page++
            } else {
              this.$refs.waterfall.end()
              this.canLoad = false
            }
            this.loading = false
          })
      }
    },
    loaded () {
      this.$refs.waterfall.isFinished = true
    },
    clickItemHandler (item) {
      this.$router.push({path: item.href})
    }
  },
  created () {
    this.loadData()
  },
  mounted () {
  }
  }
</script>



